<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <button @click="to_test">去实验页面</button>
    <button @click="to_prop">去props页面</button>
    <button @click="to_home" title="请用手机模式预览">去home页面</button>
    <button @click="to_news" title="请用手机模式预览">去资讯页面</button>
    <transition name="slide-fade">
      <router-view class="c_router"></router-view>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to My world!'
    }
  },
  methods:{
    to_test(){
      // this.$routes.path="/index";
      this.$router.push({path: '/index'})
    },
    to_prop(){
       this.$router.push({path: '/parent'})
    },
    to_home(){
      this.$router.push({path: '/home'})
    },
    to_news(){
      this.$router.push({path: '/news'})
    }
  },
  // watch:{
  //       $route(to,from){
  //           console.log(to,from);
  //       }
  //   }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello{
  text-align: center;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.c_router{
  margin-top: 10px;
}
.slide-fade-enter-active {
  transition: all 1.2s ease;
}
.slide-fade-leave-active {
  transition: all .1s cubic-bezier(2.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to
{
  left:0;right: 0;
  transform: translateX(50px);
  opacity: 0;
}
</style>



