<template>
     <div>
        <p class="par" >我是父组件</p>
        <!-- <input type="text" v-model="da"> -->
        <son sdas="有没有搞错" class="sono"></son>
        <!-- 向子组件发送数据 -->

        <son v-on:asd="dt"></son>
        <!-- 向子组件绑定事件和方法 -->

        <p>{{sonmsg}}</p>

        <!-- <input v-model="fristname">
        <input v-model="lastname">
        <div>{{fullname}}</div>
        <div>{{count}}</div> -->
    </div>
</template>
<script>
import son from './son.vue'
export default {
    name:'Parent',
    data(){
        return{
            da: '这个数据来源于parent',
            fristname:'',
            lastname: '',
            count: 0,
            // dt: ''
            sonmsg:''
        }
    },
    components:{
        son,
    },
    // computed用于属性的计算如下父fullname的值是fristname与lastname的和，计算好的fullname可以直接当作data使用
    computed:{
        fullname(){
            return `${this.fristname}  ${this.lastname}`
            // return this.fristname + this.lastname;
        }
    },
    // watch被用于对数据的监听，如下如果fristname没变化一次的话count就加一
    watch:{
        fristname(){
            this.count++
        },
        lastname(){
            this.count++
        },
        fullname(){
            this.count++
        }
    },
    methods:{
        dt(msg){
            this.sonmsg = msg;
            // console.log(msg);
        }
    }
    //  props:{
    //     da
    // } 
}
</script>
<style lang="less">
.par{
    font-weight: 900;
    font-size: 30px;
}
.sono{
    margin-bottom: 20px;
    border: solid 1px rgb(134, 117, 117);
    border-radius: 8px;
}
</style>



