<template>
    <div>
        <p class="son">我是子组件 &nbsp;    &nbsp;    &nbsp;    &nbsp;    {{sdas}}</p>
    </div>
</template>
<script>
export default {
    name: 'son',
    data(){
        return{
            msg:'这个数据来源于son'
        }
    },
    props:['sdas'],
    mounted(){
        this.$emit('asd',this.msg);
        //向父组件发送数据$emit(a,b) a:在父组件中定义的事件名，b:向父组件发送的数据名
        // console.log(this.msg);
        // this.$parent
    }
}
</script>
<style lang="less">
.son{
    font-size: 20px;
}
</style>



